body {
    font-family: 'Oswald', sans-serif !important;
    text-decoration: none;
}

#mainNav {
    min-height: 3.5rem;
    background-color: #fff;
    transition: 0.25s;
}

.navbar-nav {
    transition: 1s;
}

#mainNav .navbar-toggler {
    font-size: 80%;
    padding: 0.75rem;
    color: black;
}

#mainNav .navbar-toggler:focus {
    outline: none;
}

#mainNav .navbar-brand {
    color: #000;
    font-weight: 500;
    padding: 0.9rem 0;
}

#mainNav .navbar-nav .nav-item:hover {
    color: fade(#fff, 80%);
    outline: none;
    background-color: transparent;
}

#mainNav .navbar-nav .nav-item:active,
#mainNav .navbar-nav .nav-item:focus {
    outline: none;
    background-color: transparent;
}

@media (min-width: 992px) {
    #mainNav {
        padding-top: 0;
        padding-bottom: 0;
        border-bottom: none;
        background-color: white;
        transition: 0.3s ease;
    }
    #mainNav .navbar-brand {
        padding: 0.5rem 0;
    }
    .navbar-logo:hover {
        border: grey 2px;
    }
    #mainNav .nav-link {
        transition: none;
        margin: 2rem 2px;
        transition: 0.3s;
        color: black;
        border-bottom: 0;
    }
    #mainNav .nav-link:hover {
        color: white;
        background-color: grey;
    }
    #mainNav .nav-link:active {
        color: white;
        background-color: black;
    }
    #mainNav.navbar-shrink {
        background-color: white;
        transition: 0.25s;
        min-height: 3.5rem;
    }
}

@media (prefers-reduced-motion: no-preference) {
     :root {
        scroll-behavior: smooth;
    }
}

hr {
    margin: 1rem 0;
    color: inherit;
    background-color: currentColor;
    border: 0;
    opacity: 0.25;
    height: 1px;
}

.container,
.container-fluid,
.container-xxl,
.container-xl,
.container-lg,
.container-md,
.container-sm {
    width: 100%;
    padding-right: var(--bs-gutter-x, 0.75rem);
    padding-left: var(--bs-gutter-x, 0.75rem);
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 576px) {
    .container-sm,
    .container {
        max-width: 540px;
    }
}

@media (min-width: 768px) {
    .container-md,
    .container-sm,
    .container {
        max-width: 720px;
    }
}

@media (min-width: 992px) {
    .container-lg,
    .container-md,
    .container-sm,
    .container {
        max-width: 960px;
    }
}

@media (min-width: 1200px) {
    .container-xl,
    .container-lg,
    .container-md,
    .container-sm,
    .container {
        max-width: 1140px;
    }
}

@media (min-width: 1400px) {
    .container-xxl,
    .container-xl,
    .container-lg,
    .container-md,
    .container-sm,
    .container {
        max-width: 1320px;
    }
}

